//
// Styles
// ============================================================================

// General
// ----------------------------------------------------------------------------
body {
  background-color: #f3f4f8;
}

// Typography
// ----------------------------------------------------------------------------
h2,
h3 {
  font-family: Nunito, sans-serif;
  font-weight: 700;
}

.desc {
  color: #848487;
  font-family: Lato, sans-serif;
}

hr {
  margin: 0;
}

// Navigation
// ----------------------------------------------------------------------------
.navbar-dark {
  .navbar-nav {
    .active > .nav-link {
      font-weight: 900;
    }

    .nav-link {
      color: rgba(255, 255, 255, 1);
    }
  }
}

// Bloks
// ----------------------------------------------------------------------------
.blocks-group-wrapper {
  position: relative;
}

.block-wrapper {
  position: static;

  .background-wrapper {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -2;
    overflow: hidden;
  }

  section {
    position: relative;
    min-width: 320px;
    padding: 2.5rem 0;
    font-size: 16px;
    line-height: 1.5;
    font-weight: 400;
    word-wrap: break-word;

    @media (min-width: 768px) {
      padding: 5rem 0;
    }

    @media (min-width: 992px) {
      padding: 6rem 0;
    }
  }
}

.block-wrapper-masthead {
  .background-wrapper {
    background: url(../images/bg/bg-masthead_1920.png) no-repeat 50% 50% / cover;

    @media (min-width: 1920px) {
      background: url(../images/bg/bg-masthead_2560.png) no-repeat 50% 50% / cover;
    }
    @media (min-width: 2560px) {
      background: url(../images/bg/bg-masthead_3840.png) no-repeat 50% 50% / cover;
    }

    // stylelint-disable media-feature-name-no-vendor-prefix
    @media only screen and (-webkit-min-device-pixel-ratio: 1.5) and (min-width: 1200px),
      only screen and (min-resolution: 144dpi) and (min-width: 1200px) {
      background: url(../images/bg/bg-masthead_2560.png) no-repeat 50% 50% / cover;
    }
    @media only screen and (-webkit-min-device-pixel-ratio: 1.5) and (min-width: 1920px),
      only screen and (min-resolution: 144dpi) and (min-width: 1920px) {
      background: url(../images/bg/bg-masthead_3840.png) no-repeat 50% 50% / cover;
    }
    @media only screen and (-webkit-min-device-pixel-ratio: 1.5) and (min-width: 2560px),
      only screen and (min-resolution: 144dpi) and (min-width: 2560px) {
      background: url(../images/bg/bg-masthead_5120.png) no-repeat 50% 50% / cover;
    }
    // stylelint-enable
  }

  .column-text {
    h2 {
      @media (min-width: 992px) {
        font-size: 4.5rem;
      }
    }
  }

  .column-image {
    min-height: 337px;

    @media (min-width: 768px) {
      min-height: 560px;
    }
  }

  .device-iPhone {
    position: absolute;
    bottom: 0;
    left: 50%;
    z-index: 1;
    width: 168px;
    height: 337px;
    transform: translateX(-50%);

    @media (min-width: 768px) {
      width: 279px;
      height: 560px;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }

  .device-backdrop-wrapper {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;

    .device-backdrop-picture {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      padding-top: 0;
      overflow: hidden;
    }
  }

  .device-image-wrapper {
    position: absolute;
    top: 10px;
    left: 11px;
    z-index: 0;
    width: 148px;
    height: 318px;

    @media (min-width: 768px) {
      top: 15px;
      left: 17px;
      width: 245px;
      height: 530px;
    }

    .device-image-picture {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      padding-top: 0;
    }
  }
}

