      color: #090a15;
      font-family: Nunito, sans-serif;
      font-weight: 700;

      &::before {
        position: absolute;
        top: 50%;
        left: 50%;
        font-size: 34px;
        transform: translate(-50%, -50%);
        content: counter(list);
        counter-increment: list;
      }
    }
  }
